
import images from '../utils/image-path'
export default {
    namespaced: true,
    state: {
      bgTop: images.userBgTop,
      avatar: images.avatar,
	  name:'',
      icons: images.icons,
      menuList: [ /*{
        id: 1,
        text: '',
        path:'/wallet'
      } ,*/{
        id: 2,
        text: '',
        path:'/pages/confirm',
		isShow: true
      },{
        id: 3,
        text: '',
        path: '/pages/set-meals',
		isShow: true
      },{
        id: 4,
        text: '',
        path:'/pages/flow',
		isShow: true
      },{
        id: 5,
        text: '',
        path:'/pages/recorder',
		isShow: true
      },{
        id: 7,
        text: '',
        path:'/pages/diacrisis',
		isShow: true
      },{
        id: 8,
        text: '',
        path:'/pages/recover',
		isShow: false
      },{
        id: 9,
        text: '',
        path:'/pages/activity'  
      // },{
      //   id: 10,
      //   text: '',
      //   path: '/pages/help'
      },{
        id: 6,
        text: '',
        path:'/pages/certification'
      }],
      balance: 0.00,
      cert_status: 0
    },
    mutations: {
      balanceStatus (state, data1,data2) {
        state.balance = data1        
        state.cert_status = data2
      }
    },
	getters: {
		menuListFilter(state, getters, rootState) {                             //rootState state
			let arr = new Array()
			state.menuList.forEach(element => {
				switch (element.text) {
				  case '':
					arr.push(
						Object.assign(element,{
							isShow: rootState.supportRecover=='',
						})
					)
					break;
				  case '':
					arr.push(
						Object.assign(element,{
							isShow: rootState.can_tg=='',
						})
					)
					break;
				  case '':
					arr.push(
						Object.assign(element,{
							isShow: rootState.payMode != '',
						})
					)
					break;
				  case '':
					arr.push(
						Object.assign(element,{
							isShow: rootState.needAuth=='' && certStatus == ''//,true,
  						})
					)
					break;
				  default:
					arr.push(
						Object.assign(element,{
							isShow: true,
						})
					)
					break;
				}
			});		
			return arr
		}
	},
    actions: {
      updateBalanceBy({ commit, state },data){
        let obj = {
          balance: state.balance,
          cert_status: state.cert_status
        }        
        Object.assign(obj,data)
        commit('balanceStatus',obj.balance,obj.cert_status)
      }
    }
  }