import request from "./request";
//let baseUrl = "http://www.xxx.com/api/";
import baseUrl from '@/utils/url.js'
//newrequest
let $http = new request({
	//
	baseUrl: baseUrl,
	//
	fileUrl: baseUrl,
	//
	headers: {
		'content-type': 'application/json;charset=UTF-8'
	},
	//
	//--
	isPrompt: true,
	//
	load: true,
	//
	isFactory:  true,
	//
	loadMore:  true,
});
//
let requestNum = 0;
//
$http.requestStart = function (options) {
	if (requestNum <= 0) {
		uni.showNavigationBarLoading();
		if (options.load) {
			//
			uni.showLoading({
				title: '',
				mask: true
			});
		}
	}
	requestNum += 1;
	//token
	options.headers['token'] = "1234568";
	console.log("", options);
	return options;
}
//
$http.requestEnd = function (options, resolve) {
	//
	requestNum = requestNum - 1;
	if (requestNum <= 0) {
		uni.hideLoading();
		uni.hideNavigationBarLoading();
	}
	if (resolve.errMsg && (resolve.errMsg != "request:ok" || resolve.statusCode && resolve.statusCode != 200)) { 
		uni.showToast({
			title: "",
			icon: "none"
		});
	}
}
//
let loginPopupNum = 0;
//
$http.dataFactory = function (options, resolve) {
	console.log("", resolve);
	//
	var callback = {
		//success
		success: false,
		//
		result: ""
	};
	//
	if (resolve.statusCode=='200') {
		callback.success = true;
		callback.result = resolve.data;
	} else if (resolve.data.code == "1000" || resolve.data.code == "1001") {
		//
		if (loginPopupNum <= 0) {
			loginPopupNum++;
			uni.showModal({
				title: '',
				content: '~',
				confirmText: "",
				cancelText: "",
				success: function (res) {
					loginPopupNum--;
					if (res.confirm) {
						uni.navigateTo({
							url:"'/pages/login"
						});
					}
				}
			});
		}
	} else { //
		//
		if (options.isPrompt) {
			setTimeout(function () {
				//
				uni.showToast({
					title: resolve.data.info,
					icon: "none",
					duration: 3000
				});
			}, 500);
		}
		callback.result = resolve.data;
	}
	return callback;
};
export default $http;
